l a {\n    font-size: 0.9em !important;\n    padding-left: 30px !important;\n    background: #6d7fcc;\n}\n\nul.CTAs {\n    padding: 20px;\n}\n\nul.CTAs a {\n    text-align: center;\n    font-size: 0.9em !important;\n    display: block;\n    border-radius: 5px;\n    margin-bottom: 5px;\n}\n\na.download {    \n    color: #818181 !important;\n    border: 2px solid #2d1fee;\n    font-size: 14px;\n}\n\na.article, a.article:hover {\n    background: #2d1fee !important;\n    color: #fff !important;\n}\n\na.active {\n    color: #2f22ee;    \n}\n\n/* ---------------------------------------------------\n    MEDIAQUERIES\n----------------------------------------------------- */\n\n@media (max-width: 768px) {\n    #sidebar {\n        margin-top: -100vh; /*juan aqui desplazar hacia arriba*/        \n        \n    }\n    #sidebar.active {\n        margin-top: 0;\n        margin-left: 0;\n        width: 100% !important;\n        height: 100vh;\n    }\n    #sidebarCollapse span {\n        display: none;\n    }\n}\n\n.closed{\n    background-color: #000 !important;\n    border-color:#2d1fee !important;\n    border-width: 2px !important;\n}\n\n.nav_t{\n    width: 100%;\n    padding: 0;\n    margin: 0;        \n}\n\n@media (min-width: 768px){\n    .collapsed {\n        display: none;\n    }\n}\n\n@media (max-width: 768px){\n    .collapsed {\n        position: fixed;\n        margin-left: 0;\n        margin-right: 0;\n        padding-left: 0;\n        padding-right: 15px;\n        margin-top: 15px;\n        -moz-text-align-last: end;\n             text-align-last: end;\n        z-index: 10;\n    }\n}\n\n.sidebar_col {\n    padding: 0%;    \n}\n\n\n/*# sourceMappingURL=data:application/json;base64, */"